import PressPopup from '../press-popup/press-popup';
import PressIndexAnchor  from '../press-index-anchor/press-index-anchor';
import PressIndexBar from '../press-index-bar/press-index-bar';


export default {
  name: 'PressAreaIndexBar',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressPopup,
    PressIndexAnchor,
    PressIndexBar,
  },
  props: {
    title: {
      type: String,
      default: '选择赛事所在区域',
    },
    areaData: {
      type: Array,
      default: () => [],
    },
    // indexList: {
    //   type: Array,
    //   default: () => [],
    // },
    sProvince: {
      type: String,
      default: '',
    },
    sCity: {
      type: String,
      default: '',
    },
    windowTop: {
      type: Number,
      default: 0,
    },
    showIndexList: {
      type: Boolean,
      default: true,
    },
  },
  emits: [
    'onCancel',
    'onConfirm',
    'onUnLimit',
  ],
  data() {
    return {
      cityList: [],
      isShow: true,
      province: '',
      city: '',
      stickyOffsetTop: 0,
    };
  },
  computed: {
    innerIndexList() {
      if (!this.showIndexList) {
        return [];
      }
      if (this.isShow) {
        return this.areaData.map(item => item.key);
      }
      return this.cityList.map(item => item.key);
    },
  },
  watch: {
    sCity(val) {
      this.city = val;
    },
    sProvince(val) {
      this.province = val;
    },
  },
  mounted() {
    this.city = this.sCity;
    this.province = this.sProvince;
    setTimeout(() => {
      this.$refs.pressIndexBar.updateData();
      this.$refs.pressIndexBar.setAnchorsRect().then((res) => {
        this.stickyOffsetTop = res[0]?.top || 0;
      });
    }, 400);
  },
  methods: {
    handleProvince(val) {
      this.isShow = false;
      this.city = '';
      this.province = val.name;
      this.cityList = val.city;
    },
    handleCity(val) {
      this.city = val.name;
    },
    handleReSelect() {
      this.isShow = true;
      this.province = '';
      this.city = '';
    },
    handleUnLimit(type) {
      this.city = '';
      this.$emit('onUnLimit', type);
    },
    onCancel() {
      this.$emit('onCancel');
    },
    confirm() {
      this.$emit('onConfirm', this.province, this.city);
    },
  },
};
